<template>
  <div class="shoppingCart">
    <van-nav-bar title="采购单" fixed @click-right="onClickEdit">
      <van-icon
        v-show="cart"
        slot="left"
        size="24px"
        color="#666666"
        name="arrow-left"
        @click="toDetail"
      />
      <span slot="right">{{ edit }}</span>
    </van-nav-bar>
    <van-checkbox-group v-model="checked">
      <div class="content" v-for="(goods, index) in list" :key="index">
        <div class="top">
          <van-checkbox
            :ref="'checkbox' + index"
            :disabled="status[index] == 2 || status[index] == 3"
            :name="index"
            checked-color="#F91E1E"
            @click.stop="changeBox(index)"
          />
          <div class="img">
            <img
              v-lazy="goods.imgURl"
              :key="goods.imgURl"
              @click="toPage(index)"
            />
            <img
              v-if="(goods.consell & 2) > 0"
              class="kongxiao"
              src="..\..\assets\consell-tag-1.png"
            />
            <img
              v-else-if="(goods.consell & 1) > 0 && (goods.consell & 2) == 0"
              class="kongxiao"
              src="..\..\assets\consell-tag-0.png"
            />
            <div v-if="status[index] == 2" class="none" @click="toPage(index)">
              已下架
            </div>
            <div v-if="status[index] == 3" class="none" @click="toPage(index)">
              库存不足
            </div>
            <div v-if="goods.isExCoupon" class="isExCoupon">
              无法使用优惠券
            </div>
          </div>
          <div class="right" @click="toPage(index)">
            <div class="goodsName">{{ goods.ptitle }} {{ goods.spec }}</div>
            <div class="company">{{ goods.verdor }}</div>
            <div class="package">中包装：{{ goods.medpacknum }}盒</div>
            <div class="price">
              <span>￥{{ Aprice[index] }}</span>
              <span>￥{{ goods.mp }}</span>
            </div>
          </div>
        </div>
        <div class="bottom">
          <div>
            <!-- <p
              v-if="(goods.consell & 1) == 1"
              style="background-color: #ed2f26;"
            >
              控销药
            </p> -->
            <!-- <van-tag plain type="danger" v-if="goods.rule && goods.rule.length > 0" v-for="(item,i) in goods.rule.slice(0,4)" :key="i">
              {{ item.rulename }}
            </van-tag> -->
            <!-- <van-tag
              v-if="goods.rule && goods.rule.length > 0"
              v-for="(item, i) in goods.rule.slice(0, 3)"
              :key="i"
              plain
              type="danger"
              >{{ item.rulename }}</van-tag
            > -->
          </div>
          <van-stepper
            v-model="goods.num"
            integer
            :disabled="
              limitStep[index] || status[index] == 2 || status[index] == 3
            "
            :step="goods.medpacknum"
            :max="goods.limitsub"
            :min="goods.medpacknum"
            disable-input
            async-change
            @minus="reduceStep(goods.num, index)"
            @plus="changeStep(goods.num, index)"
            @overlimit="overlimit(goods.num, index)"
          />
        </div>
        <div class="activeInfo">
          <p
            v-if="!goods.exActivity"
            v-for="(value, key, a) in goods.currLadDesc"
          >
            <van-tag v-if="activeCode(key)" plain type="danger">{{
              activeCode(key)
            }}</van-tag>
            <span v-if="activeCode(key)" v-for="(item, b) in value"
              >{{ item + goods.nextLadDesc[key][b] }}
              <span v-if="item || goods.nextLadDesc[key][b]">;</span>
            </span>
          </p>
          <p
            v-if="goods.exActivity"
            v-for="(value, key, a) in goods.currLadDesc"
            :key="a"
          >
            <van-tag
              v-if="activeCode(key)"
              v-for="(item, b) in value"
              :key="b"
              plain
              type="danger"
              >{{ item + goods.nextLadDesc[key][b] }}</van-tag
            >
          </p>
        </div>
      </div>
    </van-checkbox-group>

    <van-submit-bar
      v-show="show"
      :price="price"
      :loading="loading"
      :disabled="submit"
      button-text="去结算"
      @submit="onSubmit"
    >
      <van-checkbox v-model="allS" @click="allSelect" checked-color="#F91E1E"
        >全选</van-checkbox
      >
      <transition name="tip">
        <div :class="tips" v-if="active.length > 0">
          <span v-for="(item, index) in active" :key="index">
            <span>{{ item.offername.slice(0, 2) }} : </span>
            <span style="color:#bbb;"
              >{{
                item.nextladDesc ? item.nextladDesc : item.currladDesc
              }}； </span
            ><br v-if="(index & 1) == 1" />
          </span>
          <div v-if="tipsC" class="tipsClick" @click="tipsClick">
            {{ tipsClickText }}
          </div>
        </div>
      </transition>
    </van-submit-bar>
    <div v-show="!show" class="bottoms">
      <van-checkbox v-model="allS" @click="allSelect" checked-color="#F91E1E"
        >全选</van-checkbox
      >
      <span>
        <button @click.stop="collection">添加至收藏</button>
        <button @click.stop="delGoods">删除</button>
      </span>
    </div>
    <div v-if="flag" class="flag"></div>
  </div>
</template>
<script>
import * as types from "@/store/mutations";
export default {
  data() {
    return {
      checked: [],
      edit: "编辑",
      list: [],
      show: true,
      allS: false,
      unqids: "",
      skus: "",
      price: 0,
      flag: false,
      active: [],
      loading: false,
      cart: false,
      limitStep: [], //  限制购买数
      timer: null,
      status: [], //  商品状态
      Aprice: [],
      arrlimits: [],
      ruleCodeToText: {
        1110: "满减-现金",
        1120: "满减-包邮",
        1130: "满减-折扣",
        1210: "满赠-返利",
        1220: "满赠-包邮券",
        1230: "满赠-折扣券",
        1240: "满赠-赠品",
        1133: "团购",
        1113: "秒杀"
      },
      listImg: [],
      checkedList: [],
      tips: "tips",
      tipsClickText: "展开",
      tipsC: true,
      actives: {},
      againShopping: false
    };
  },
  created() {
    this.$store.commit(types.SET_TABBAR_INDEX, 2);
  },
  computed: {
    userInfo() {
      return this.$loadUserInfo();
    },
    submit() {
      return this.checked.length > 0 ? false : true;
    }
  },
  mounted() {
    // this.getGoodsInfo();
    this.cart = this.$route.query.cart;
  },
  activated() {
    this.getGoodsInfo();
    // this.allS = !this.$route.query.againShopping
    // this.allSelect()
    //this.$route.query.againShopping && this.allSelect()
    //this.allS = this.$route.query.againShopping && this.allSelect() || false;
  },
  methods: {
    toPage(i) {
      this.$router.push({
        name: "goodsDetail",
        query: {
          sku: this.list[i].sku
        }
      });
    },
    toDetail() {
      this.$router.replace({
        name: "goodsDetail"
      });
    },
    onClickEdit() {
      this.checkedList = [];
      this.show = !this.show;
      this.checked = [];
      this.allS = false;
      this.getOfferTip();
      this.price = 0;
      this.unqids = "";
      if (this.edit === "编辑") {
        this.edit = "取消";
        this.status.forEach((data, i) => {
          this.status[i] = 0;
        });
      } else {
        this.edit = "编辑";
        this.list.forEach((data, i) => {
          this.status[i] = data.status;
        });
      }
    },
    //  收藏
    collection() {
      if (this.skus.slice(1) !== "") {
        JNB.query({
          serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
          cls: "MyCollectModule",
          method: "add",
          params: {
            ids: this.skus.slice(1)
          },
          callback: data => {
            let result = JSON.parse(data);
            if (result.code == 200) {
              this.$toast("收藏成功~");
            } else {
              this.$toast.fail(result.message);
            }
          }
        });
      } else {
        this.$toast("请选择需要收藏的商品~");
      }
    },
    //  增加个数
    changeStep(i, index) {
      // this.limitStep[index] = true
      // if(i <= this.list[index].limitsub){
      //   this.$manageGoodNum(this, { sku: this.list[index].sku }, i).then(() => {
      //     Vue.set(this.limitStep,index,false)
      //   })
      if (this.timer !== null) {
        window.clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        this.$manageGoodNum(this, { sku: this.list[index].sku }, i).then(() => {
          this.getGoodsInfo();
        });
        this.changeBox(index);
      }, 500);
      // }
    },
    //  减少
    reduceStep(i, index) {
      if (this.timer !== null) {
        window.clearTimeout(this.timer);
      }
      this.timer = setTimeout(() => {
        this.$manageGoodNum(this, { sku: this.list[index].sku }, i).then(() => {
          this.getGoodsInfo();
        });
        this.changeBox(index);
      }, 500);
      // }
    },
    //  限制
    overlimit(i, index) {
      if (i >= this.list[index].limitsub) {
        this.$toast("超过购买限制了哟~");
      } else if (i <= this.list[index].medpacknum) {
        this.$toast("受不了了，商品不能再减少了~");
      }
    },
    //  删除采购单商品
    delGoods() {
      if (this.unqids !== "") {
        JNB.query({
          serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
          cls: "ShoppingCartModule",
          method: "clearShopCart",
          params: {
            compid: this.userInfo.comp.storeId,
            ids: this.unqids.slice(1)
          },
          callback: data => {
            this.checkedList = [];
            let result = JSON.parse(data);
            if (result.code === 200) {
              this.getGoodsInfo();
              this.price = 0;
              this.$toast("删除成功！");
              this.active = [];
              this.checked = [];
              this.allS = false;
            } else {
              this.$toast.fail(result.message);
            }
          }
        });
      } else {
        this.$toast("请选择需要删除的商品！");
      }
    },
    //  选择所有商品
    allSelect() {
      this.allS = !this.allS;
      this.unqids = "";
      this.skus = "";
      this.price = 0;
      this.checked = [];
      if (this.allS) {
        this.list.forEach((arr, index) => {
          if (arr.status !== 2 && arr.status !== 3) {
            this.checked.push(index);
          } else if (this.edit == "取消") {
            this.checked.push(index);
          }
          this.checkedList[index] = true;
        });
        //  调用商品活动信息
        this.getOfferTip();
      } else {
        this.checked = [];
        this.unqids = "";
        this.skus = "";
        this.active = [];
        this.checkedList = [];
      }
      this.checked.forEach(num => {
        this.unqids += "," + this.list[num].unqid;
        this.skus += "," + this.list[num].sku;
        this.price += this.Aprice[num] * this.list[num].num * 100;
      });
    },
    //  勾选采购单商品
    changeBox(index) {
      this.unqids = "";
      this.skus = "";
      setTimeout(() => {
        this.price = 0;
        this.checked.forEach(num => {
          this.unqids += "," + this.list[num].unqid;
          this.skus += "," + this.list[num].sku;
          this.price += this.Aprice[num] * this.list[num].num * 100;
        });
        if (this.list.length === this.checked.length) {
          this.allS = true;
        } else {
          this.allS = false;
        }
        this.getOfferTip();
        this.checkedList[index] = this.$refs["checkbox" + index][0].checked;
      }, 100);
    },
    //  获取商品信息
    getGoodsInfo() {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "ShoppingCartModule",
        method: "queryUnCheckShopCartList",
        params: {
          compid: this.userInfo.comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200 && result.data) {
            if (result.data.length < 1) {
              this.flag = true;
            } else {
              this.flag = false;
            }
            if (result.data.length > 0) {
              result.data.forEach((data, i) => {
                if (data.skprice > 0) {
                  this.Aprice.push(data.skprice);
                } else {
                  this.Aprice.push(data.pdprice);
                }
                this.status.push(data.status);
                data.sku = data.pdno;
                if (data.limitnum === 0) {
                  data.limitsub = data.inventory;
                }
                if (data.actstock < data.limitsub) {
                  data.limitsub = data.actstock;
                }
                this.getActive(data.sku, i);
              });
              this.$request_img({
                context: this,
                arr: result.data,
                cacheImg: true
              });
            }
            this.list = result.data;
            this.$route.query.againShopping && this.allSelect()
          } else {
            this.list = [];
            this.flag = true;
          }
        }
      });
    },
    //  活动商品信息
    getActive(sku, i) {
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "CalculateModule",
        method: "getActivitiesBySKU",
        params: [sku, this.userInfo.comp.storeId],
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            if (result.data.discounts.length > 0) {
              this.discounts = result.data.discounts;
              //  计算活动限购数量
              // let arrlimit = [];
              // result.data.discounts.forEach(data => {
              //   if (data.limitnum !== 0) {
              //     arrlimit.push(data.limitnum);
              //   }
              // });
              // this.arrlimits[i] = arrlimit.sort()[0];
              // this.arrlimits.forEach((data, i) => {
              //   if (data) {
              //     this.list[i].limitsub = data;
              //   }
              // });
              if (result.data.minimum < this.list[i].limitsub) {
                this.list[i].limitsub = result.data.minimum;
              }
            }
          }
        }
      });
    },
    //  去结算
    onSubmit() {
      this.loading = true;
      let goodsList = this.checked.map(i => {
        return {
          pdno: this.list[i].sku,
          pnum: this.list[i].num,
          compid: this.userInfo.comp.storeId,
          checked: 1,
          unqid: this.list[i].unqid,
          conpno: 0,
          areano: this.userInfo.comp.addressCode,
          pdprice: this.list[i].pdprice
        };
      });
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "ShoppingCartModule",
        method: "querySettShopCartList",
        params: goodsList,
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200) {
            this.checked.forEach((data, i) => {
              result.data.goods[i].imgURl = this.list[data].imgURl;
            });
            this.$router.push({
              path: "/goodsOrder",
              query: {
                placeType: 2
              }
            });
            sessionStorage.setItem(
              "placeOrderList",
              JSON.stringify(result.data.goods)
            );
            sessionStorage.setItem(
              "activeOrderList",
              JSON.stringify(result.data.gifts)
            );
            this.loading = false;
          } else {
            this.loading = false;
            this.$toast.fail(result.message);
          }
        }
      });
    },
    //  活动信息
    getOfferTip() {
      let arr = [];
      this.checked.forEach(num => {
        arr.push({
          compid: this.userInfo.comp.storeId,
          pdno: this.list[num].pdno,
          pnum: this.list[num].num,
          checked: 1,
          unqid: this.list[num].unqid
        });
      });
      JNB.query({
        serverName: "orderServer" + this.$ser_in(this.userInfo.comp.storeId),
        cls: "ShoppingCartModule",
        method: "getOfferTip",
        params: arr,
        callback: data => {
          let result = JSON.parse(data);
          if (result.code == 200 && result.data) {
            this.actives = result.data;
            this.active = result.data.slice(0, 6);
            if (result.data.length < 7) {
              this.tips = "tipsA";
              this.tipsC = false;
              this.tipsClickText = "收起";
            } else {
              this.tips = "tips";
              this.tipsC = true;
              this.tipsClickText = "展开";
            }
          } else {
            this.active = [];
          }
        }
      });
    },
    activeCode(data) {
      var result = this.ruleCodeToText[data];
      return result;
    },
    tipsClick() {
      if (this.tips == "tips") {
        this.tips = "tipsA";
        this.tipsClickText = "收起";
        this.active = this.actives;
      } else {
        this.tips = "tips";
        this.tipsClickText = "展开";
        this.active = this.actives.slice(0, 6);
      }
    }
  }
};
</script>
<style scoped>
